.project-list {
  display: grid;
  grid-gap: 16px;
  justify-content: center;

  @media screen and (min-width: $widthTablet) {
    grid-template-columns: repeat(auto-fit, minmax(335px, 1fr));
    grid-gap: 16px;
  }

  &__item {
    width: 100%;
    height: 100%;

    @media screen and (min-width: $widthTablet) {
      display: flex;
      justify-content: center;
    }
  }

  &__image {
    border-radius: 10px;
    width: 100%;
    height: 240px;
    object-fit: cover;

    @media screen and (min-width: $widthTablet) {
      max-width: 374px;
    }
  }

  &__overlay {
    position: relative;
  }

  &__description {
    display: flex;
    justify-content: center;
    align-items: center;

    position: absolute;

    bottom: 0;
    left: 0;
    width: 100%;
    height: 40px;
    border-bottom-left-radius: 10px;
    border-bottom-right-radius: 10px;

    background: rgba(47, 48, 58, 0.5);
    color: white;
    font-weight: 700;
    font-size: 18px;
    line-height: 1.14;
    text-align: center;
    letter-spacing: 0.03em;
    text-transform: uppercase;
  }
}
